<template>
    <Head>
        <title head-key="title">Reset Password!</title>
        <meta type="description" content="Please login to use app" head-key="description">
    </Head>

    <div class="d-flex flex-column flex-root" id="login-page-root">
			<div class="d-flex flex-column flex-lg-row flex-column-fluid">
                <div class="bg-primary d-block d-lg-none py-10 text-center">
                    <a href="/">
                        <img alt="Logo" src="/dmn/media/logos/clinicsoft-logo-white.png" class="theme-light-show h-35px" />
                        <img alt="Logo" src="/dmn/media/logos/clinicsoft-logo-white.png" class="theme-dark-show h-35px" />
                    </a>
                </div>
				<!--begin::Aside-->
				<div class="d-flex flex-column flex-column-fluid flex-center w-lg-50 p-10">
					<div class="d-flex justify-content-center flex-column-fluid flex-column w-100 mw-450px">
						
						<div class="py-10">
							<form class="form w-100 needs-validation" id="kt_sign_in_form" @submit.prevent="submit">
								<div class="card-body">
									<div class="text-start mb-10">
										<h1 class="text-dark mb-3 fs-2x" data-kt-translate="sign-in-title">{{__('Reset password')}}</h1>
										<div class="text-gray-400 fw-semibold fs-6" data-kt-translate="general-desc">{{__('Please set your new password')}}</div>
									</div>
									<div class="fv-row mb-8">
                                        <TextInput type="email" class="form-control-lg" v-model="form.email" autocomplete="username" :placeholder="__('E-Mail')"/>
                                        <InputError :message="form.errors.password" />
									</div>
                                    <div class="fv-row mb-8">
                                        <TextInput type="password" class="form-control-lg" v-model="form.password" autocomplete="new-password" :placeholder="__('Password')"/>
                                        <InputError :message="form.errors.password" />
									</div>
                                    <div class="fv-row mb-8">
                                        <TextInput type="password" class="form-control-lg" v-model="form.password_confirmation" required autocomplete="new-password" :placeholder="__('Confirm Password')"/>
                                        <InputError :message="form.errors.password_confirmation" />
									</div>
									<div class="d-flex flex-stack">
										<PrimaryButton class="w-100" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">
                                            {{__('Reset Password')}}
                                        </PrimaryButton>
									</div>
								</div>
							</form>
						</div>
                        
					</div>
				</div>
				<!--end::Aside-->
				<div class="d-none d-lg-flex flex-lg-row-fluid w-50 bgi-size-cover bgi-position-y-center bgi-position-x-start bgi-no-repeat" style="background-image: url(/dmn/media/auth/bg11.png)"></div>
			</div>
		</div>

</template>

<script>
import GuestLayout from '../../Layouts/GuestLayout';
export default {
    layout:GuestLayout
}
</script>


<script setup>
import { useForm,Link } from '@inertiajs/vue3';

const props = defineProps({
    email: String,
    token: String,
});

const form = useForm({
    token: props.token,
    email: props.email,
    password: '',
    password_confirmation: '',
});

const submit = () => {
    form.post(route('password.update'), {
        onFinish: () => form.reset('password', 'password_confirmation'),
    });
};
</script>